import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-shopping-cart-component',
  templateUrl: './shopping-cart-component.component.html',
  styleUrls: ['./shopping-cart-component.component.css']
})
export class ShoppingCartComponentComponent implements OnInit {
  cartItems:any=[];
  grandTotal:number=0;
  isOrderSubmitted:boolean=false;
  isOrderApproved:boolean=false;
  isOrderDelivered:boolean=false;
  constructor() { 
    console.log('constructor');
  }

  ngOnInit() {
    console.log('ngOnInit');
    this.cartItems=[{
                      "Id":"11111",
                      "itemName":"Mutton Soup",
                      "unitPrice":"100",
                      "quantity":"1",
                      "tax":"10"
                    },
                    {
                      "Id":"11112",
                      "itemName":"Chicken Biriryani",
                      "unitPrice":"200",
                      "quantity":"1",
                      "tax":"20"
                    },
                    {
                      "Id":"11113",
                      "itemName":"Orance Juice",
                      "unitPrice":"50",
                      "quantity":"2",
                      "tax":"10"
                    }];
    this.grandTotal=this.getGrantTotal();
  }

  removeFromCart(item){
    console.log('removeFromCart');
    console.log(item.Id);
    document.getElementById("success_msg").style.display="block";
    setTimeout(function(){ document.getElementById("success_msg").style.display="none"; }, 500);
  }

  getGrantTotal(){
    let total:number=0;
    console.log('getGrantTotal');
    for(let i=0;i<this.cartItems.length;i++){
      this.cartItems[i].totalPrice = this.cartItems[i].unitPrice*this.cartItems[i].quantity + +this.cartItems[i].tax;
      total+= +this.cartItems[i].totalPrice;
    }
    return total;
  }

  placeOrder(){
    document.getElementById("success_msg_order").style.display="block";
    setTimeout(function(){ document.getElementById("success_msg_order").style.display="none"; }, 500);
    this.isOrderSubmitted=true;
  }

}



